	module.exports = {"pathContext":{"docHtml":"<h1>is-dotfile <a href=\"https://www.npmjs.com/package/is-dotfile\"><img src=\"https://img.shields.io/npm/v/is-dotfile.svg?style=flat\" alt=\"NPM version\"></a> <a href=\"https://npmjs.org/package/is-dotfile\"><img src=\"https://img.shields.io/npm/dm/is-dotfile.svg?style=flat\" alt=\"NPM monthly downloads\"></a> <a href=\"https://npmjs.org/package/is-dotfile\"><img src=\"https://img.shields.io/npm/dt/is-dotfile.svg?style=flat\" alt=\"NPM total downloads\"></a> <a href=\"https://travis-ci.org/jonschlinkert/is-dotfile\"><img src=\"https://img.shields.io/travis/jonschlinkert/is-dotfile.svg?style=flat&#x26;label=Travis\" alt=\"Linux Build Status\"></a></h1>\n<blockquote>\n<p>Return true if a file path is (or has) a dotfile. Returns false if the path is a dot directory.</p>\n</blockquote>\n<h2>Install</h2>\n<p>Install with <a href=\"https://www.npmjs.com/\">npm</a>:</p>\n<pre><code class=\"language-sh\">$ npm install --save is-dotfile\n</code></pre>\n<h2>Usage</h2>\n<p>To be considered a dotfile, it must be the last filename in the path, like <code>.gitignore</code>. Otherwise it's a <a href=\"https://github.com/jonschlinkert/is-dotdir\">dot directory</a>, like <code>.git/</code> and <code>.github/</code>.</p>\n<pre><code class=\"language-js\">var isDotfile = require('is-dotfile');\n</code></pre>\n<p><strong>false</strong></p>\n<p>All of the following return <code>false</code>:</p>\n<pre><code class=\"language-js\">isDotfile('a/b/c.js');\nisDotfile('/.git/foo');\nisDotfile('a/b/c/.git/foo');\n//=> false\n</code></pre>\n<p><strong>true</strong></p>\n<p>All of the following return <code>true</code>:</p>\n<pre><code class=\"language-js\">isDotfile('a/b/.gitignore');\nisDotfile('.gitignore');\nisDotfile('/.gitignore');\n//=> true\n</code></pre>\n<h2>About</h2>\n<h3>Related projects</h3>\n<ul>\n<li><a href=\"https://www.npmjs.com/package/dotdir-regex\">dotdir-regex</a>: Regex for matching dot-directories, like <code>.git/</code> | <a href=\"https://github.com/regexps/dotdir-regex\" title=\"Regex for matching dot-directories, like &#x60;.git/&#x60;\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/dotfile-regex\">dotfile-regex</a>: Regular expresson for matching dotfiles. | <a href=\"https://github.com/regexps/dotfile-regex\" title=\"Regular expresson for matching dotfiles.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-dotdir\">is-dotdir</a>: Returns true if a path is a dot-directory. | <a href=\"https://github.com/jonschlinkert/is-dotdir\" title=\"Returns true if a path is a dot-directory.\">homepage</a></li>\n<li><a href=\"https://www.npmjs.com/package/is-glob\">is-glob</a>: Returns <code>true</code> if the given string looks like a glob pattern or an extglob pattern… <a href=\"https://github.com/jonschlinkert/is-glob\">more</a> | <a href=\"https://github.com/jonschlinkert/is-glob\" title=\"Returns &#x60;true&#x60; if the given string looks like a glob pattern or an extglob pattern. This makes it easy to create code that only uses external modules like node-glob when necessary, resulting in much faster code execution and initialization time, and a bet\">homepage</a></li>\n</ul>\n<h3>Contributing</h3>\n<p>Pull requests and stars are always welcome. For bugs and feature requests, <a href=\"../../issues/new\">please create an issue</a>.</p>\n<h3>Contributors</h3>\n<table>\n<thead>\n<tr>\n<th><strong>Commits</strong></th>\n<th><strong>Contributor</strong></th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>13</td>\n<td><a href=\"https://github.com/jonschlinkert\">jonschlinkert</a></td>\n</tr>\n<tr>\n<td>1</td>\n<td><a href=\"https://github.com/Lykathia\">Lykathia</a></td>\n</tr>\n</tbody>\n</table>\n<h3>Building docs</h3>\n<p><em>(This project's readme.md is generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb</a>, please don't edit the readme directly. Any changes to the readme must be made in the <a href=\".verb.html\">.verb.md</a> readme template.)</em></p>\n<p>To generate the readme, run the following command:</p>\n<pre><code class=\"language-sh\">$ npm install -g verbose/verb#dev verb-generate-readme &#x26;&#x26; verb\n</code></pre>\n<h3>Running tests</h3>\n<p>Running and reviewing unit tests is a great way to get familiarized with a library and its API. You can install dependencies and run tests with the following command:</p>\n<pre><code class=\"language-sh\">$ npm install &#x26;&#x26; npm test\n</code></pre>\n<h3>Author</h3>\n<p><strong>Jon Schlinkert</strong></p>\n<ul>\n<li><a href=\"https://github.com/jonschlinkert\">github/jonschlinkert</a></li>\n<li><a href=\"https://twitter.com/jonschlinkert\">twitter/jonschlinkert</a></li>\n</ul>\n<h3>License</h3>\n<p>Copyright © 2017, <a href=\"https://github.com/jonschlinkert\">Jon Schlinkert</a>.\nReleased under the <a href=\"LICENSE\">MIT License</a>.</p>\n<hr>\n<p><em>This file was generated by <a href=\"https://github.com/verbose/verb-generate-readme\">verb-generate-readme</a>, v0.6.0, on May 30, 2017.</em></p>","docPath":"argo-ci/node_modules/is-dotfile/readme","proj":"argo-ci"}}

